.messageListItem {
    font-size: 1.5rem;
    border: 1px solid rgba(255, 255, 255, 0.297);
    background-color: rgb(0 0 0 / 0.2);
    border-radius: 6px;
    padding: 1.3rem;
    margin-bottom: 2rem;
}

.messageListItem div {
    display:  flex;
    font-size: 1.6rem;
    width: 100%;
    justify-content: space-between;
    padding: 0.3rem;
}

.contentBox {
    padding: 2rem;
    border-radius: 6px;
    background-color:rgba(97, 97, 97, 0.393);
    color: rgba(255, 255, 255, 0.596);
}

@media (max-width:460px) {
    .messageListItem {
        font-size: 1.35rem;
    }

    .messageListItem div { 
        font-size: 1.45rem;
        padding: 0.3rem;
    }

}