.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
  }
  
  
.modal {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -55%);
    width: 50%;
    z-index: 100;
    padding: 2rem;
    overflow: hidden;
    background-color: #11112a;
    border: 1px solid white;
  }

.email {
    width: 100%;
    margin-bottom: 1rem;
    padding: .6rem .6rem;
    font-size: 1.5rem;
    font-weight: bold;
    font-family: 'Roboto';
}

.modal div {
    width: 100%;
  }
  
  .modal textarea {
    padding: 1rem;
    font-size: 2rem;
    border: 1px solid #444;
    height: 20rem;
    width: 100%;
    flex: 1 1 100%;
  }
  
  .modal button {
    padding: 0.6rem 1rem;
    font-size: 1.8rem;
    font-weight: 600;
    background-color: rgba(235, 9, 9, 0.758);
    color: aliceblue;
    margin-top: 1.1rem;
    cursor: pointer;
  }
  
  .message {
    font-size: 1.4rem;
    color: rgba(225, 225, 18, 0.902);
    padding: .3rem;
  }
  
  @media (max-width: 670px) {
    .modal {
        width: 70%;
      }
  }

  @media (max-width: 950px) {
    .modal {
        width: 60%;
      }
  }


  @media (max-width: 440px) {
    .modal textarea {
      height: 13rem;
      font-size: 2rem;
    }
    .modal button {
      font-size: 1.4rem;
    }
    
  .modal {
    width: 70%;
  }
    
  }