nav {
    font-size: 2.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0 0 0 / 0.2);
    backdrop-filter: blur(2rem);
    color: rgb(255 255 255 / 0.5);
    border-bottom: .1px solid  rgba(255, 255, 255, 0.297);
    /* box-shadow: 0 0 10px rgba(255, 255, 255, 0.258); */
}

nav ul {
    display: flex;
    margin: 2.5rem;
    column-gap: 3rem;
    
}

nav ul li {
    padding: 0 1rem;
    transition:all .2s ease;
}

nav ul li a, 
nav ul li a:visited {
    text-decoration: none;
    color: rgb(255 255 255 / 0.5);
}

nav ul li a:hover {
    color: white;
    cursor: pointer;
}

nav ul li:active {
    transform: scale(1.06);
}

@media (max-width:470px){
    nav{
    font-size: 2rem;
    }

}

@media (max-width:355px){
    nav{
    font-size: 1.6rem;
    }

}